<template>
  <div class="myForm" />
</template>
<script>
import { ConversationalForm } from 'conversational-form'
export default {
  name: 'MyForm',
  data() {
    return {}
  },
  mounted() {
    this.setupForm()
  },
  methods: {
    setupForm() {
      const formFields = [
        {
          'tag': 'input',
          'type': 'text',
          'name': 'firstname',
          'cf-questions': ''
        },
        {
          'tag': 'input',
          'type': 'text',
          'name': 'lastname',
          'cf-questions': ''
        },
        {
          'tag': 'fieldset',
          'type': 'radio',
          'name': 'isPig',
          'cf-questions': '',
          'children': [
            {
              'tag': 'input',
              'type': 'radio',
              'cf-label': '',
              'value': 'yes'
            },
            {
              'tag': 'input',
              'type': 'radio',
              'cf-label': '',
              'value': 'no',
              'disabled': 'true'
            }
          ]
        }
      ]
      this.cf = ConversationalForm.startTheConversation({
        options: {
          submitCallback: this.submitCallback,
          preventAutoFocus: true
        },
        tags: formFields
      })
      this.$el.appendChild(this.cf.el)
    },
    submitCallback() {
      const formDataSerialized = this.cf.getFormData(true)
      console.log('Formdata, obj:', formDataSerialized)
      this.cf.addRobotChatResponse('')
    }
  }
}
</script>

<style lang="scss" scoped>
.myForm{
    position: relative;
    height: 600px;
    width:100%;
}
</style>
